import EventEmitter from "@/util/EventEmitter";
import { defineComponent } from "vue";
import useVuelidate from "@vuelidate/core";
import { maxValue, minValue } from "@vuelidate/validators";

export default defineComponent({
  name: "TagSettings",
  props: {
    settings: Object,
    name: String,
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  data() {
    return {
      x: 0,
      y: 0,
      decimals: 0,
      show: false,
      blockId: "",
      id: "",
      did: "",
      form: {} as any,
      validations: {} as any,
      tagData: {} as any,
      tagElement: {} as any,
      tagType: String, // TODO: Type
    };
  },
  validations() {
    return this.validations;
  },
  methods: {
    emitValue() {
      this.tagData["decimals"] = this.form[this.did];
      this.tagData["type"] = this.tagType;
      EventEmitter.emitter.emit(`tag-property-settings-${this.id}`, this.tagElement, this.tagData);
    },
    cancel() {
      this.show = false;
    },
    save() {
      this.v$.$touch();
      if (!this.v$.$error) {
        this.emitValue();
        this.show = false;
      }
    },
  },
  created() {
    EventEmitter.emitter.on("tag-property-settings", (blockId: string, data: any) => {
      const validations: any = {
        form: {},
      };
      this.x = data.x;
      this.y = data.y;
      this.id = data.id;
      this.blockId = blockId;
      this.show = true;
      this.tagData = data.tag.data;
      this.tagElement = data.tag.tag;
      this.did = `decimals-${this.id}-${this.tagData.value}`;
      this.form[this.did] = data.tag.data.decimals || "";
      validations["form"][this.did] = {
        minValue: minValue(0),
        maxValue: maxValue(100),
      };
      this.validations = validations;
      this.tagType = data.tag.data.type;
    });
  },
});
