import { defineComponent } from "vue";
import Tagify from "@yaireo/tagify";
import "@yaireo/tagify/dist/tagify.css";
import EventEmitter from "@/util/EventEmitter";
import TagSettings from "@/components/TagSettings/index.vue";

const tagify: any = null;

export default defineComponent({
  name: "Tags",
  props: {
    mode: String,
    settings: Object,
    name: String,
    id: String,
    blockId: String,
    value: [String, Array],
    onChange: Function,
    invalid: Boolean,
  },
  components: {
    TagSettings,
  },
  data() {
    return {
      tagify,
      modifiedValue: this.$props.value,
    };
  },
  methods: {
    getTagIndexByValue(tags: Array<any>, value: string) {
      const map = tags.reduce(
        (map, { value }) => map.set(value, (map.get(value) || 0) + 1),
        new Map()
      );
      const t = tags.map((o) => Object.assign({}, o, { count: map.get(o.value) }));
      return t;
    },
    getWhitelistItemByValue(value: string) {
      const t: any = this.tagify.settings.whitelist.find((o: any) => o.value === value);
      return t;
    },
    escapeRegExp(str: string) {
      return str.replace(/[.*+\-?^${}()|[\]\\]/g, "\\$&");
    },
    replaceAll(str: string, find: string, replace: string) {
      return str.replace(new RegExp(this.escapeRegExp(find), "g"), replace);
    },
    formClass() {
      this.tagify.toggleClass("form-field-error");
    },
  },
  watch: {
    invalid() {
      this.formClass();
    },
    value: {
      deep: true,
      handler(newVal) {
        this.modifiedValue = newVal;
      },
    },
    settings: {
      deep: true,
      immediate: false,
      handler(val) {
        let tags;
        if (
          val &&
          val.whitelist &&
          this.tagify.settings &&
          JSON.stringify(val.whitelist) !== JSON.stringify(this.tagify.settings.whitelist)
        ) {
          const difference = this.tagify.settings.whitelist.filter(
            (x: any) => !val.whitelist.includes(x)
          );
          for (let x = 0; x < difference.length; x++) {
            if (this.modifiedValue) {
              tags = this.tagify.getCleanValue();
              const tagsIndexes = this.getTagIndexByValue(tags, difference[x].value);
              if (tagsIndexes.length > 0) {
                for (let y = 0; y < tagsIndexes.length; y++) {
                  const tag = tagsIndexes[y];
                  const decimals = tag.decimals
                    ? `,"decimals":"${tag.decimals}"`
                    : `,"decimals":""`;
                  const type = tag.type ? `,"type":"${tag.type}"` : "";
                  const formattedTag = `[[{"value":"${tag.value}","text":"${tag.text}","title":"${tag.title}"${type},"style":"${tag.style}"${decimals},"prefix":"${tag.prefix}"}]]`;
                  this.tagify.removeTags(difference[x].value);
                  this.modifiedValue = this.replaceAll(
                    this.modifiedValue as string,
                    formattedTag,
                    ""
                  );
                  EventEmitter.emitter.emit(
                    `block-properties-remove-tag-from-field-${this.blockId}`,
                    this.blockId,
                    {
                      id: this.id,
                      value: formattedTag,
                    }
                  );
                  // TODO: this is done for backward compatibility, remove when possible
                  const formattedOldTag = `[[{"value":"${tag.value}","text":"${tag.text}","title":"${tag.title}"${type},"style":"${tag.style}","prefix":"${tag.prefix}"}]]`;
                  this.modifiedValue = this.replaceAll(
                    this.modifiedValue as string,
                    formattedOldTag,
                    ""
                  );
                }
              }
            }
          }
          this.tagify.settings.whitelist = val.whitelist;
          this.tagify.loadOriginalValues(this.modifiedValue);
        }
      },
    },
  },
  beforeUnmount() {
    this.tagify.destroy();
  },
  created() {
    EventEmitter.emitter.on(`tag-property-settings-${this.id}`, (tagElement: any, tagData: any) => {
      this.tagify.replaceTag(tagElement, tagData);
    });
  },
  mounted() {
    this.settings!.templates.tag = (tagData: any) => {
      const type = this.getWhitelistItemByValue(tagData.value).type;
      try {
        return `<tag title='${
          tagData.value
        }' contenteditable='false' spellcheck="false" class='tagify__tag ${
          tagData.class ? tagData.class : ""
        }' ${this.tagify.getAttributes(tagData)}>
                    <x title='remove tag' class='tagify__tag__removeBtn'></x>
                    <div>
                        <span class='tagify__tag-text'>${tagData.value}</span>
                    </div>
                    ${
                      type === "NUM"
                        ? `<div><i role="button" class="fas fa-cog mt-1 tagSettingsBtn"></i></div>`
                        : ""
                    }
                </tag>`;
      } catch (err) {
        console.error(err);
      }
    };
    this.tagify = new Tagify(this.$el, this.settings);
    this.tagify.on("click", (e: any) => {
      EventEmitter.emitter.emit("tag-property-settings", this.blockId, {
        id: this.id,
        x: e.detail.originalEvent.clientX - 120,
        y: e.detail.originalEvent.clientY + 32,
        tag: e.detail,
      });
    });
    if (this.$props.invalid) this.formClass();
    if (this.value) this.tagify.loadOriginalValues(this.value);
  },
});
